const uni = require('uni').default;






















import pageHead from '../../../components/page-head.vue'

export default {
	data: {
		title: 'list-triplex-row',
		lists: []
	},
	onLoad() {
		let list = [];
		for (let i = 0; i < 5; i++) {
			list.push(i)
		}
		this.lists = list;
	},
	components: {
		pageHead
	}
}



